import { Operacao } from '../types';

export interface Resultado {
  operacao: Operacao;
  ir: number;  // imposto devido
  pm: number;  // preo mdio atualizado
  qm: number;  // quantidade mdia atual
  pa: number;  // prejuzo acumulado
}

/**
 * Calcula o IR por operao, atualizando preo mdio (PM),
 * quantidade mdia (QM) e prejuzo acumulado (PA) para cada ativo.
 */
export function calcularIRPorOperacao(operacoes: Operacao[]): Resultado[] {
  const posicoes: Record<string, { pm: number; qm: number; pa: number }> = {};
  const resultados: Resultado[] = [];

  // Ordena operaes por data crescente
  const opsOrdenadas = [...operacoes].sort(
    (a, b) => new Date(a.data).getTime() - new Date(b.data).getTime()
  );

  for (const op of opsOrdenadas) {
    const ticker = op.ticker;
    if (!posicoes[ticker]) {
      posicoes[ticker] = { pm: 0, qm: 0, pa: 0 };
    }

    const pos = posicoes[ticker];

    if (op.tipo === 'compra') {
      // Compra: recalcula preo mdio ponderado
      const totalCompra = pos.pm * pos.qm + op.preco * op.quantidade + op.taxaCorretagem;
      pos.qm += op.quantidade;
      pos.pm = totalCompra / pos.qm;

      resultados.push({ operacao: op, ir: 0, pm: pos.pm, qm: pos.qm, pa: pos.pa });
    } else {
      // Venda: calcula resultado da operao (RA)
      const ra = (op.preco - pos.pm) * op.quantidade - op.taxaCorretagem;
      pos.qm -= op.quantidade;

      let ir = 0;

      if (ra < 0) {
        // Prejuzo: acumula no PA (valor negativo)
        pos.pa += ra;
      } else if (ra > 0) {
        // Lucro: compensa com prejuzo acumulado (se houver) antes de calcular IR
        const abatimento = Math.min(ra, Math.abs(pos.pa));
        ir = (ra - abatimento) * 0.15; // alquota fixa 15%
        pos.pa += abatimento; // reduz prejuzo acumulado (PA sobe, mas deve permanecer  0)
        if (pos.pa > 0) pos.pa = 0;
      }

      resultados.push({ operacao: op, ir, pm: pos.pm, qm: pos.qm, pa: pos.pa });
    }
  }

  return resultados;
}
